.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 24px;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal {
  background: var(--white);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-xl);
  max-width: 500px;
  width: 100%;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  border-bottom: 1px solid var(--gray-200);
}

.modalTitle {
  font-family: var(--font-display);
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--midnight);
  margin: 0;
}

.closeBtn {
  padding: 8px;
  border: none;
  background: transparent;
  color: var(--gray-400);
  cursor: pointer;
  border-radius: var(--radius-sm);
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeBtn:hover {
  background: var(--gray-100);
  color: var(--gray-700);
}

.modalContent {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-700);
}

.searchInput {
  padding: 10px 12px;
  border: 1px solid var(--gray-300);
  border-radius: var(--radius-md);
  font-size: 0.9375rem;
  font-family: var(--font-body);
  color: var(--midnight);
  background: var(--white);
  transition: all 0.2s;
}

.searchInput:hover {
  border-color: var(--azure);
}

.searchInput:focus {
  outline: none;
  border-color: var(--azure);
  box-shadow: 0 0 0 3px rgba(24, 128, 234, 0.1);
}

.select {
  padding: 10px 12px;
  border: 1px solid var(--gray-300);
  border-radius: var(--radius-md);
  font-size: 0.9375rem;
  font-family: var(--font-body);
  color: var(--midnight);
  background: var(--white);
  cursor: pointer;
  transition: all 0.2s;
}

.select:hover {
  border-color: var(--azure);
}

.select:focus {
  outline: none;
  border-color: var(--azure);
  box-shadow: 0 0 0 3px rgba(24, 128, 234, 0.1);
}

.radioGroup {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.radioLabel {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 0.9375rem;
  color: var(--midnight);
}

.radioLabel input[type="radio"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.checkboxLabel {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 0.9375rem;
  color: var(--midnight);
}

.checkboxLabel input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.modalActions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.cancelBtn {
  padding: 10px 20px;
  border: 1px solid var(--gray-300);
  background: var(--white);
  color: var(--gray-700);
  border-radius: var(--radius-md);
  font-size: 0.9375rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.cancelBtn:hover {
  background: var(--gray-50);
  border-color: var(--gray-400);
}

.submitBtn {
  padding: 10px 20px;
  border: none;
  background: var(--azure);
  color: var(--white);
  border-radius: var(--radius-md);
  font-size: 0.9375rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.submitBtn:hover {
  background: var(--azure-dark);
}

